function setProfileSettings(slicer, type) {
  switch (type) {
    case "good":
      slicer.metadata.preferred_quality_type = "fast";
      slicer.settings.resolution.children.layer_height.default_value = 0.32;
      slicer.settings.speed.children.speed_print.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 60;
      return slicer;
    case "better":
      slicer.metadata.preferred_quality_type = "normal";
      slicer.settings.resolution.children.layer_height.default_value = 0.16;
      slicer.settings.speed.children.speed_print.default_value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 60;
      return slicer;
    case "best":
      slicer.metadata.preferred_quality_type = "best";
      slicer.settings.resolution.children.layer_height.default_value = 0.08;
      slicer.settings.speed.children.speed_print.default_value = 40;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 50;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 40;
      return slicer;
    case "petg":
      slicer.settings.travel.children.retraction_enable.default_value = true;
      slicer.settings.speed.children.speed_print.default_value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 60;
      slicer.settings.material.children.default_material_print_temperature.default_value = 245;
      slicer.settings.material.children.material_print_temperature.default_value = 245;
      slicer.settings.material.children.material_print_temperature_layer_0.default_value = 245;
      slicer.settings.material.children.material_initial_print_temperature.default_value = 245;
      slicer.settings.material.children.material_final_print_temperature.default_value = 245;
      slicer.settings.material.children.default_material_bed_temperature.default_value = 70;
      slicer.settings.material.children.material_bed_temperature.default_value = 70;
      return slicer;
    case "tpu":
      slicer.settings.travel.children.retraction_enable.default_value = false;
      slicer.settings.speed.children.speed_print.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 30;
      slicer.settings.material.children.default_material_print_temperature.default_value = 230;
      slicer.settings.material.children.material_print_temperature.default_value = 230;
      slicer.settings.material.children.material_print_temperature_layer_0.default_value = 230;
      slicer.settings.material.children.material_initial_print_temperature.default_value = 230;
      slicer.settings.material.children.material_final_print_temperature.default_value = 230;
      slicer.settings.material.children.default_material_bed_temperature.default_value = 50;
      slicer.settings.material.children.material_bed_temperature.default_value = 50;
      return slicer;
    case "pla":
      slicer.settings.travel.children.retraction_enable.default_value = true;
      slicer.settings.speed.children.speed_print.default_value = 50;
      slicer.settings.speed.children.speed_print.children.speed_infill.default_value = 60;
      slicer.settings.speed.children.speed_print.children.speed_wall.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_0.default_value = 30;
      slicer.settings.speed.children.speed_print.children.speed_wall.children.speed_wall_x.default_value = 60;
      slicer.settings.material.children.default_material_print_temperature.default_value = 220;
      slicer.settings.material.children.material_print_temperature.default_value = 220;
      slicer.settings.material.children.material_print_temperature_layer_0.default_value = 220;
      slicer.settings.material.children.material_initial_print_temperature.default_value = 220;
      slicer.settings.material.children.material_final_print_temperature.default_value = 220;
      slicer.settings.material.children.default_material_bed_temperature.default_value = 50;
      slicer.settings.material.children.material_bed_temperature.default_value = 50;
      return slicer;
    default:
      return slicer;
  }
}

function setInfillMultiplier(infill_pattern) {
  switch (infill_pattern) {
    case "triangles":
    case "trihexagon":
    case "cubic":
    case "cubicsubdiv":
      return 3;
    case "grid":
    case "tetrahedral":
    case "quarter_cubic":
      return 2;
    default:
      return 1;
  }
}

function setInfillLineDistance(slicer, percent_density) {
  // infill line distance is what we really need to set
  if (percent_density === 0) {
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.default_value = 0;
  } else {
    let multiplier = setInfillMultiplier(
      slicer.settings.infill.children.infill_pattern.default_value
    );
    let infillLineWidth =
      slicer.settings.resolution.children.line_width.children.infill_line_width
        .default_value;
    let infillLineDistance =
      ((infillLineWidth * 100) / percent_density) * multiplier;
    slicer.settings.infill.children.infill_sparse_density.children.infill_line_distance.default_value = infillLineDistance;
  }
  return slicer;
}

function Treeify(files) {
  var fileTree = {};

  if (files instanceof Array === false) {
    throw new Error("Expected an Array of file paths, but saw " + files);
  }

  files.forEach(function parseFilePath(file) {
    var filePath = file.display;
    var fileLocation = filePath.split("/");

    // If file is in root directory, eg 'index.js'
    if (fileLocation.length === 1) {
      return (fileTree[fileLocation[0]] = file);
    }

    fileLocation.reduce(function mergePathsIntoFileTree(
      prevDir,
      currDir,
      i,
      filePath
    ) {
      if (i === filePath.length - 1) {
        prevDir[currDir] = file;
      }

      if (!prevDir.hasOwnProperty(currDir)) {
        prevDir[currDir] = {};
      }

      return prevDir[currDir];
    },
    fileTree);
  });

  return fileTree;
}

function isFolder(filename) {
  let fileTypes = [
    ".g",
    ".gcode",
    ".G",
    ".GCODE",
    ".stl",
    ".STL",
    ".3mf",
    ".3MF",
    ".zip",
    ".ZIP"
  ];

  for (let idx in fileTypes) {
    if (filename.endsWith(fileTypes[idx])) {
      return false;
    }
  }

  return true;
}

function getFolderOnlyPath(o, id, path) {
  //Early return
  if (o === id) {
    return path;
  }
  var result, p;
  for (p in o) {
    if (o.hasOwnProperty(p) && typeof o[p] === "object") {
      if (isFolder(p)) {
        path.push(p);
        result = getFolderOnlyPath(o[p], id, path);
        if (result) {
          return result;
        }
      }
    }
  }
  return path;
}

function getFileDirectory(rootFiles, newFiles) {
  let directory = [];
  let fileTypes = [
    ".g",
    ".gcode",
    ".G",
    ".GCODE",
    ".stl",
    ".STL",
    ".3mf",
    ".3MF",
    ".zip",
    ".ZIP"
  ];
  let fileKeys = Object.keys(newFiles);

  for (let fileIdx in fileKeys) {
    for (let idx in fileTypes) {
      if (fileKeys[fileIdx].endsWith(fileTypes[idx])) {
        if (newFiles[fileKeys[fileIdx]].hasOwnProperty("display")) {
          let parts = newFiles[fileKeys[fileIdx]].display.split("/");
          directory = parts;
          directory.pop();
          return directory;
        }
      } else {
        directory = getFolderOnlyPath(rootFiles, newFiles, []);
      }
    }
  }
  return directory;
}

function getSize(files) {
  let size = 0;
  Object.keys(files).forEach((key, i) => {
    if (files[key].hasOwnProperty("size")) {
      size += files[key].size;
    } else {
      if(typeof files[key] == "object") {
        size += getSize(files[key]);
      }
    }
  });

  return size;
}

function isFileProtected(filename) {
  for (let idx in protectedFiles) {
    if(protectedFiles[idx].name == filename) {
      return true;
    }
  }
  return false;
}

const FileManager = ({
  drawerOpen,
  setDrawerOpen,
  fileLocation,
  manage,
  update
}) => {
  const classes = useStyles();
  const [selected, setSelected] = useState("");
  const [loading, setLoading] = useState(true);
  const [files, setFiles] = useState([]);
  const [rootFiles, setRootFiles] = useState([]);
  const [storageUsed, setStorageUsed] = useState(0);
  const [storageFree, setStorageFree] = useState(0);
  const [storageTotal, setStorageTotal] = useState(0);
  const [directories, setDirectories] = useState({});
  const [currentDirectory, setCurrentDirectory] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [filament, setFilament] = useState("");
  const [quality, setQuality] = useState("good");
  const [infill, setInfill] = useState(20);
  const [vase, setVase] = useState(false);
  const [supports, setSupports] = useState(true);
  const [showTooltip, setShowTooltip] = useState(false);
  const [settings, setSettings] = useState({});
  const [showSnack, setShowSnack] = useState(false);
  const [snackMessage, setSnackMessage] = useState("");
  const handleSnackClose = () => {
    setShowSnack(() => false);
    setSnackMessage("");
  };

  useEffect(() => {
    if (drawerOpen) {
      setLoading(true);
      //make api call to fetch files
      //use fileLocation (usb or local) to determine which call to make
      async function getFiles() {
        // Default options are marked with *
        let response = null;

        if (fileLocation === "local") {
          response = await fetch(`http://${window.location.hostname}:2127/api/v1/files`, {
            method: "GET"
          });
        } else {
          await fetch(`http://${window.location.hostname}:2127/api/v1/usb/refresh`, {
            method: "POST"
          });
          response = await fetch(`http://${window.location.hostname}:2127/api/v1/usb/files`, {
            method: "GET"
          });
        }
        return response.json(); // parses JSON response into native JavaScript objects
      }

      getFiles().then((data) => {
        if (fileLocation === "local") {
          setFiles(data.files);
          setStorageFree(data.free);
          setStorageTotal(data.total);
          setStorageUsed(data.total - data.free);
        } else {
          let fileTree = Treeify(data.files);
          setFiles(fileTree);
          setRootFiles(fileTree);
        }
        setLoading(false);
      });

      async function getSettings() {
        // Default options are marked with *
        let onboardSettings = await fetch(
          `http://${window.location.hostname}:2127/api/v1/settings`,
          {
            method: "GET"
          }
        );

        return onboardSettings.json(); // parses JSON response into native JavaScript objects
      }

      if (fileLocation === "usb") {
        getSettings().then((data) => {
          setSettings(data);
          setSupports(
            data.settings.support.children.support_enable.default_value
          );
          setInfill(
            data.settings.infill.children.infill_sparse_density.default_value
          );

          if (
            data.settings.travel.children.retraction_enable.default_value ===
            false
          ) {
            setFilament("tpu");
          } else {
            if (
              data.settings.material.children.default_material_print_temperature
                .default_value === 245
            ) {
              setFilament("petg");
            } else {
              setFilament("pla");
            }
          }

          if (
            data.settings.resolution.children.layer_height.default_value ===
            0.32
          ) {
            setQuality("good");
          } else if (
            data.settings.resolution.children.layer_height.default_value ===
            0.08
          ) {
            setQuality("best");
          } else {
            // 0.16
            setQuality("better");
          }
        });
      }
    }
  }, [drawerOpen, fileLocation]);

  const handleZipCheck = () => {
    console.log(selected);
    setLoading(true);
    setDrawerOpen(false);
    async function checkUpdates() {
      // Default options are marked with *
      const response = await fetch(
        `http://localhost:2127/api/v1/usb/copy/${selected}`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json"
          }
        }
      );
      return response.json(); // parses JSON response into native JavaScript objects
    }
    checkUpdates().then((data) => {
      console.log(data);
      setLoading(false);
      //after api call
      if (data.updated) {
        setSnackMessage("Software Updated. Rebooting...");
        setShowSnack(true);
        setTimeout(() => {
          //console.log("handleDownload state 6 of ; Calling reboot");
          fetch(`http://localhost:8990/reboot`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json"
            }
          });
        }, 3000);
      } else {
        setSnackMessage("Software Update Failed. Reverted to previous version.");
        setShowSnack(true);
      }
    });
  }

  const handleSelect = () => {
    console.log(selected);
    setLoading(true);
    if (fileLocation === "local") {
      //call api to print file
      let selected_file = selected.split('\\').pop().split('/').pop();
      fetch(`http://${window.location.hostname}:2127/api/v1/job/${selected_file}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" }
      }).then((data) => {
        setLoading(false);
        setDrawerOpen(() => false);
        //call function or swap to status screen
        setSelected("");
        setShowTooltip(false);
        setShowModal(false);
      });
    } else {
      fetch(`http://${window.location.hostname}:2127/api/v1/usb/copy/${selected}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" }
      }).then((data) => {
        let selected_file = selected.split('\\').pop().split('/').pop();
        fetch(`http://${window.location.hostname}:2127/api/v1/job/${selected_file}`, {
          method: "POST",
          headers: { "Content-Type": "application/json" }
        }).then((data) => {
          setLoading(false);
          setDrawerOpen(() => false);
          //call function or swap to status screen
          setSelected("");
          setShowTooltip(false);
          setShowModal(false);
        });
      });
    }
  };

  const deleteFile = () => {
    setLoading(true);

    fetch(`http://${window.location.hostname}:2127/api/v1/files/local/${selected}`, {
      method: "DELETE",
      headers: { "Content-Type": "application/json" }
    }).then(response => response.json()).then((data) => {
      fetch(`http://${window.location.hostname}:2127/api/v1/files`, {
        method: "GET",
        headers: { "Content-Type": "application/json" }
      }).then(response => response.json()).then((data) => {
        setLoading(false);
        setFiles(data.files);
        setStorageFree(data.free);
        setStorageTotal(data.total);
        setStorageUsed(data.total - data.free);
      });
    });

  };

  const handleSlice = () => {
    let newSettings = settings;

    if (filament === "tpu") {
      newSettings = setProfileSettings(newSettings, quality);
      newSettings = setProfileSettings(newSettings, filament);
    } else {
      newSettings = setProfileSettings(newSettings, filament);
      newSettings = setProfileSettings(newSettings, quality);
    }

    newSettings = setInfillLineDistance(newSettings, infill);
    newSettings.settings.infill.children.infill_sparse_density.default_value = infill;
    newSettings.settings.support.children.support_enable.default_value = supports;

    newSettings.settings.blackmagic.children.magic_spiralize.default_value = vase;

    setSettings(newSettings);
    //api logic to slice
    //call api to copy and print file
    setLoading(true);
    fetch(`http://${window.location.hostname}:2127/api/v1/usb/copy/${selected}`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(newSettings) // send updated settings
    }).then((data) => {
      //call handleSelect once you have gcode file
      handleSelect();
    });
  };

  const resetFiles = (newFiles) => {
    if (currentDirectory) {
      directories[currentDirectory] = newFiles;
      setDirectories(directories);
    }
    setFiles(newFiles);
  };

  const folderSelected = (newFiles, directory) => {
    setCurrentDirectory(directory);
    setSelected(newFiles);
  };

  const exitFiles = () => {
    setFiles([]);
    setDrawerOpen(false);
  };

  return (
    <div>
      {!manage && !update && (
        <AppBar position="sticky">
          <Typography variant="h6" style={{ textAlign: "center" }}>
            Printer Name: {getStorage("printerName")}
          </Typography>
        </AppBar>
      )}
      <div className={classes.header}>
        {!manage && !update && (
          <h2 style={{marginLeft: 10}}>
            {(
              fileLocation.toUpperCase() === 'USB' ? 'USB Media' : 'Printer Storage'
            )}
          </h2>
        )}
        {!manage && !update && (
        <IconButton aria-label="back" onClick={() => exitFiles()} style={{ position:'absolute', right:0}}>
          <Close />
        </IconButton>
        )}
      </div>
      {fileLocation === "usb" && (
        <Container className={classes.fileContainer}>
          <Grid container direction="row" style={{background: '#525252'}}>
            <Grid
              item
              style={{ alignSelf: "center" }}
              onClick={() => resetFiles(rootFiles)}
            >
              <Typography variant="h6">
                <IconButton aria-label="back"  className={classes.icon}>
                  <Home />
                </IconButton>
              </Typography>
            </Grid>
            {getFileDirectory(rootFiles, files).map((folder, index, {length}) => {
              if (isFolder(folder)) {
                return (
                  <Grid
                    item
                    style={{ alignSelf: "center" }}
                    onClick={() => resetFiles(directories[folder])}
                    key={folder}
                  >
                    <Typography variant="h6">
                      <IconButton aria-label="back"  className={classes.icon}>
                        <ArrowForward />
                      </IconButton>
                      {index + 1 === length && (
                        <IconButton aria-label="back"  className={classes.icon}>
                          <Folder />
                        </IconButton>
                      )}
                      {folder}
                    </Typography>
                  </Grid>
                );
              } else {
                return <></>;
              }
            })}
          </Grid>
        </Container>
      )}
      {loading ? (
        <Backdrop className={classes.backdrop} open={true}>
          <img
            alt="print"
            src={imgPrint}
            style={{ width: 200, height: 200, zIndex: 99999999 }}
          ></img>
        </Backdrop>
      ) : (
        <Container className={classes.fileContainer}>
          <TableContainer className={classes.fileList}>
            <Table stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell>Size</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {fileLocation === "local"
                  ? files &&
                    files.map((file, index) => {

                      if(!manage || !isFileProtected(file.name)) {
                        return (
                          <TableRow
                            key={file.display}
                            onClick={() => setSelected(file.display)}
                            selected={selected === file.display}
                            className={classes.summary}
                            classes={{
                              selected: classes.selected,
                              hover: classes.hover
                            }}
                          >
                            <TableCell>
                              <IconButton aria-label="back"  className={classes.icon}>
                                {manage ? (
                                  <DeleteForever />
                                ) : (
                                  <Slideshow />
                                )}
                              </IconButton>
                              {file.display}
                            </TableCell>
                            <TableCell>{formatBytes(file.size)}</TableCell>
                          </TableRow>
                        );
                      }

                    })
                  : files &&
                    Object.keys(files).map((file, index) => {
                      if (isFolder(file)) {
                        return (
                          <TableRow
                            key={file}
                            onClick={() => folderSelected(files[file], file)}
                            selected={selected === files[file]}
                            className={classes.summary}
                            classes={{
                              selected: classes.selected,
                              hover: classes.hover
                            }}
                          >
                            <TableCell style={{
                              width: update ? '400px' : '100%'
                            }}>
                              <IconButton aria-label="back"  className={classes.icon}>
                                <FolderOpen />
                              </IconButton>
                              {file}
                            </TableCell>
                            <TableCell style={{minWidth: 110}}>
                              {formatBytes(getSize(files[file]))}
                            </TableCell>
                          </TableRow>
                        );
                      } else {
                        if(
                          (files[file].display.includes(".zip") ||
                          files[file].display.includes(".ZIP")) &&
                          /^300x-(\d+).(\d+).(\d+).zip$/.test(file)
                        ) {
                          if(update) {
                            return (
                              <TableRow
                                key={file}
                                onClick={() => setSelected(files[file].display)}
                                selected={selected === files[file].display}
                                className={classes.summary}
                                classes={{
                                  selected: classes.selected,
                                  hover: classes.hover
                                }}
                              >
                                <TableCell style={{width: "400px"}}>
                                  <IconButton aria-label="back"  className={classes.icon}>
                                    <SystemUpdateAlt />
                                  </IconButton>
                                  {file}
                                </TableCell>
                                <TableCell>{formatBytes(files[file].size)}</TableCell>
                              </TableRow>
                            );
                          }
                        } else {
                          if(!update && !(
                            files[file].display.includes(".zip") ||
                            files[file].display.includes(".ZIP"))
                          ) {
                            return (
                              <TableRow
                                key={file}
                                onClick={() => setSelected(files[file].display)}
                                selected={selected === files[file].display}
                                className={classes.summary}
                                classes={{
                                  selected: classes.selected,
                                  hover: classes.hover
                                }}
                              >
                                <TableCell>
                                  {file.endsWith(".g") ||
                                  file.endsWith(".gcode") ||
                                  file.endsWith(".G") ||
                                  file.endsWith(".GCODE") ? (
                                    <IconButton aria-label="back"  className={classes.icon}>
                                      <Slideshow />
                                    </IconButton>
                                  ) : (
                                    <IconButton aria-label="back"  className={classes.icon}>
                                      <Layers />
                                    </IconButton>
                                  )}
                                  {file}
                                </TableCell>
                                <TableCell>{formatBytes(files[file].size)}</TableCell>
                              </TableRow>
                            );
                          }
                        }
                      }
                    })}
              </TableBody>
            </Table>
          </TableContainer>
        </Container>
      )}
      <Grid
        container
        direction="column"
        justify="space-around"
        alignItems="center"
      >
      {manage ? (
        <Grid
          container
          justify="center"
          alignItems="center"
        >
          <Button
            variant="contained"
            disabled={selected.length <= 0}
            onClick={() => deleteFile()}
            className={classes.printButton}
          >
            Delete
          </Button>

          <Typography variant="h6">
            {
              formatBytes(storageUsed)
            } used of {
              formatBytes(storageTotal)
            } total, leaving {
              formatBytes(storageFree)
            } available
          </Typography>
          </Grid>
      ) : typeof selected !== "object" &&
      (selected.endsWith(".g") ||
        selected.endsWith(".gcode") ||
        selected.endsWith(".G") ||
        selected.endsWith(".GCODE")) ? (
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          onClick={() => handleSelect()}
          className={classes.printButton}
        >
          Print
        </Button>
      ) : typeof selected === "object" ? (
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          onClick={() => resetFiles(selected)}
          className={classes.printButton}
        >
          Open Folder
        </Button>
      ) : update &&
        (selected.endsWith(".zip") || selected.endsWith(".ZIP")) &&
        (/^(.+)300x-(\d+).(\d+).(\d+).zip$/.test(selected) ||
        /^300x-(\d+).(\d+).(\d+).zip$/.test(selected)
        ) ? (
         <Button
           variant="contained"
           disabled={selected.length <= 0}
           onClick={() => handleZipCheck()}
           className={classes.printButton}
         >
           Update from ZIP
         </Button>
      ) : (
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          onClick={() => setShowModal(true)}
          className={classes.printButton}
        >
          Slice
        </Button>
      )}
      </Grid>
      <Modal open={showModal} onClose={() => setShowModal(false)}>
        <Paper className={classes.modal}>
          <Grid container direction="column" className={classes.modalContainer}>
            <Grid item style={{ alignSelf: "center" }}>
              <Typography variant="h2">Slice Menu</Typography>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Filament Type" />
                <Tooltip
                  PopperProps={{
                    disablePortal: true
                  }}
                  onClose={() => setShowTooltip(false)}
                  open={showTooltip}
                  disableFocusListener
                  disableHoverListener
                  title="Must select filament type"
                  onOpen={() => setShowTooltip(() => false)}
                >
                  <Select
                    native
                    value={filament}
                    onChange={(e) => {
                      if (e.target && e.target.value) {
                        setFilament(e.target.value);
                      }
                    }}
                    MenuProps={{
                      anchorOrigin: {
                        vertical: "bottom",
                        horizontal: "left"
                      },
                      getContentAnchorEl: null
                    }}
                    className={classes.sliceDropdown}
                  >
                    <option value="" />
                    <option value="pla">PLA</option>
                    <option value="petg">PETG</option>
                    <option value="tpu">TPU</option>
                  </Select>
                </Tooltip>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Print Quality" />
                <Select
                  native
                  value={quality}
                  onChange={(e) => {
                    if (e.target && e.target.value) {
                      setQuality(e.target.value);
                    }
                  }}
                  MenuProps={{
                    anchorOrigin: {
                      vertical: "bottom",
                      horizontal: "left"
                    },
                    getContentAnchorEl: null
                  }}
                  className={classes.sliceDropdown}
                >
                  <option value="good">Fastest Print</option>
                  <option value="better">Normal Quality</option>
                  <option value="best">Best Quality</option>
                </Select>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Infill" />
                <Grid container item>
                  <Slider
                    defaultValue={infill}
                    color="secondary"
                    onChange={(e, newVal) => setInfill(newVal)}
                    getAriaValueText={() => `${infill}%`}
                    valueLabelDisplay="auto"
                    step={5}
                    min={0}
                    max={100}
                    style={{ maxWidth: "200px" }}
                  />
                  <TextField
                    value={`${infill}%`}
                    InputProps={{
                      readOnly: true
                    }}
                    variant="outlined"
                    className={classes.slider}
                  />
                </Grid>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Supports" />
                <Switch
                  checked={supports}
                  onChange={(e) => setSupports(e.target.checked)}
                  name="supports"
                />
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Vase Mode" />
                <Switch
                  checked={vase}
                  onChange={(e) => setVase(e.target.checked)}
                  name="vase"
                />
              </List>
            </Grid>
            <Grid
              item
              container
              justify="space-around"
              alignItems="center"
              style={{ flex: 1 }}
            >
              <Button
                variant="contained"
                color="secondary"
                onClick={() => setShowModal(false)}
              >
                Cancel
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  if (filament.length > 0) {
                    handleSlice();
                  } else {
                    setShowTooltip(true);
                  }
                }}
              >
                Print
              </Button>
            </Grid>
          </Grid>
          {loading && (
            <>
              <img
                alt="print"
                src={imgPrint}
                style={{
                  width: 200,
                  height: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% - 100px)"
                }}
              ></img>
              <h4
                style={{
                  width: 200,
                  zIndex: 99999999,
                  position: "absolute",
                  left: "calc(50% - 100px)",
                  top: "calc(50% + 120px)",
                  textAlign: "center"
                }}
              >
                Preparing File
              </h4>
            </>
          )}
        </Paper>
      </Modal>
      <Snackbar
        open={showSnack}
        autoHideDuration={6000}
        message={snackMessage}
        onClose={handleSnackClose}
      ></Snackbar>
    </div>
  );
};

export default FileManager;
